.tableContainer {
    max-width: 90vw;
    padding: 15px;
    margin: 0 auto;
    min-height: calc(100vh - 60px);
    display: flex;
    align-items: center;
}

.mainWrapper {
    max-width: 90vw;
    max-height: 90vh;
    display: flex;
    justify-content: center;
}

.leftBar {
    width: 15%;
    min-width: 240px;
    max-height: 90vh;
    border-radius: 8px 0 0 8px;
    background-color: #ECECF9;
    padding: 15px;
}

.leftBarTitle {
    font-weight: bold;
    margin: 10px 20px 20px 20px;
}

.filterBtnsWrapper {
    margin-bottom: 20px;
    float: bottom;
}

.tableWrapper {
    border-radius: 0 8px 8px 0;
    max-width: 85%;
    max-height: 90vh;
    background-color: #FEFEFF;
    padding: 15px;
    overflow-y: scroll;
}
